$xsm: "(max-width: 768px)";
$sm: "(min-width: 768px) and (max-width: 992px)";
$md: "(min-width: 992px) and (max-width: 1200px)";
$lg: "(min-width: 1200px)";

$tablet: "(min-width: 768px)";
$desktop: "(min-width: 1440px)";

$base-font-size: 16px;
$opsans: 'Open Sans',
Helvetica,
Arial,
sans-serif;
$sansation: 'Sansation',
Helvetica,
Arial,
sans-serif;
$ubuntu: 'Ubuntu',
Helvetica,
Arial,
sans-serif;

$themecolor: #e9444d;

$text-color: #1a1a1a;
$text-color-d: #000000;
$text-color-l: #666666;

$light-text: #ffffff;

@mixin btn-theme($themecolor: $themecolor) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 16px;
    color: $light-text;
    font-family: $opsans;
    font-weight: 700;

    border-radius: 20px;
    background-color: $themecolor;
    cursor: pointer;
    text-decoration: none;
    border: none;

    box-shadow: 0px 4px 12px rgba(230, 95, 37, 0.5);

    transition: all 300ms ease;


    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        color: $light-text;
        background-color: darken($themecolor, 10%);
    }

    &:active {
        background-color: darken($themecolor, 20%);
    }

    @media #{$desktop} {
        box-shadow: 0px 7.44161px 22.3248px rgba(230, 95, 37, 0.5);
    }

    // &[disabled] {
    //     background-color: rgba($themecolor, 0.75);
    // }
}

@mixin btn-light {
    position: relative;
    display: flex;
    justify-content: center;

    align-items: center;
    color: $light-text;
    font-family: $opsans;
    font-weight: 400;
    border-radius: 50px;

    border: 1px solid transparent;
    background-color: #f2f2f2;
    cursor: pointer;
    text-decoration: none;

    box-shadow: 3px 5px 27px 2px rgba(white, 0.29);
    transition: all 300ms ease;

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        color: $text-color;
        background-color: darken(#f2f2f2, 10%);
    }

    &:active {
        background-color: darken(#f2f2f2, 20%);
    }
}

@mixin visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

@mixin heading {
    max-width: 650px;
    margin: 0 auto;
    color: #191919;
    font-size: 27px;
    font-weight: 400;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;

    @media (max-width: 480px) {
        font-size: 10px;
    }
}

@mixin svg-icon {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    fill: url('#svg-linear-gradient')
}

@mixin img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

@mixin section {
    padding-top: 40px;
    padding-bottom: 40px;

    font-family: $opsans;
    color: $text-color;

    @media #{$desktop} {
        padding-top: 60px;
        padding-bottom: 60px;
    }
}

@mixin aspect-ratio($width, $height) {
    position: relative;

    &:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }

    >.content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}